body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.max {
  max-width: 1200px;
  margin: auto; }

h1 {
  font-weight: 600;
  font-size: 32px;
  margin-bottom: 20px; }

h2 {
  font-weight: 500;
  font-size: 30px;
  margin: 15px 0; }

h3 {
  font-weight: 400;
  font-size: 22px;
  margin: 15px 0; }

header {
  width: 100%;
  background: white;
  border-bottom: 1px solid #FAFBFC; }
  header .inner {
    padding: 20px 40px;
    max-width: 1200px;
    margin: auto;
    box-sizing: border-box; }

a {
  font-weight: 400;
  color: #000;
  text-decoration: none;
  transition: .2s all; }

.left {
  font-weight: 600; }

.right {
  float: right;
  color: #999;
  padding-left: 20px;
  font-size: 15px; }

.right:hover {
  color: #000; }

.banner {
  background: #FAFBFC;
  padding: 15px;
  font-size: 14px;
  text-align: center;
  -webkit-animation-name: appear;
          animation-name: appear;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s; }

@-webkit-keyframes appear {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes appear {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }
  .banner .bold {
    font-weight: 600; }
  .banner .light-link {
    color: #999;
    transition: 0.2s all;
    text-decoration: none; }
    .banner .light-link:hover {
      color: #0E00FF; }

footer {
  background: #FAFBFC;
  padding: 80px;
  text-align: center;
  color: #999;
  font-size: 14px; }
  footer a {
    color: inherit; }

.mainstage {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 400px;
  height: 50vh;
  flex-direction: column; }
  .mainstage .lead {
    font-size: 45px;
    font-weight: 600;
    margin-bottom: 20px;
    -webkit-animation-name: lead-animation;
            animation-name: lead-animation;
    -webkit-animation-duration: 1s;
            animation-duration: 1s; }

@-webkit-keyframes lead-animation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

@keyframes lead-animation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }
  .mainstage .sublead {
    opacity: 0;
    font-size: 20px;
    color: #999;
    -webkit-animation-name: lead-animation;
            animation-name: lead-animation;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; }

.writings {
  padding: 120px 40px; }

.socials {
  margin: 20px;
  -webkit-animation-name: socials;
          animation-name: socials;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-delay: .3s;
          animation-delay: .3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  opacity: 0;
  font-size: 22px; }
  .socials a {
    margin: 10px;
    transition: .2s all; }
  .socials a:hover {
    color: #999; }

@-webkit-keyframes socials {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

@keyframes socials {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

.carousel {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 40px 40px 120px;
  grid-gap: 40px;
  -webkit-animation-name: carousel-animation;
          animation-name: carousel-animation;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-delay: .7s;
          animation-delay: .7s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  opacity: 0; }

@-webkit-keyframes carousel-animation {
  0% {
    opacity: 0; }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

@keyframes carousel-animation {
  0% {
    opacity: 0; }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

button {
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 500;
  transition: .3s all;
  cursor: pointer; }

button:hover {
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px); }

.dark {
  background: black;
  color: white; }

.light {
  background: white;
  border: 1px solid #e8e8e8;
  color: black;
  box-shadow: 0px 1px 10px #e8e8e8; }

.project {
  padding: 20px;
  border-radius: 5px;
  transition: .3s all;
  cursor: pointer;
  display: block; }
  .project:hover {
    box-shadow: 0px 1px 40px #e8e8e8;
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px); }
  .project:hover .learn {
    color: #0E00FF; }
  .project h3 {
    transition: .2s all; }
  .project .block {
    height: 250px;
    transition: 3s all ease-out;
    background: pink; }
  .project .subtitle {
    color: #999;
    font-weight: 400;
    margin: 15px 0; }

.learn {
  margin-top: 10px;
  color: #000;
  font-weight: 500;
  transition: .2s all;
  display: block; }
  .learn:hover {
    color: #0E00FF; }

input {
  color: white;
  border-radius: 3px;
  padding: 10px 20px;
  border: 1px solid #eaeaea;
  width: 100%;
  font-size: 20px;
  color: #000;
  box-shadow: 0px 1px 20px #e8e8e8;
  box-sizing: border-box; }

.newsletter {
  background: #FAFBFC;
  border-top: 1px solid #eaeaea;
  border-bottom: 1px solid #eaeaea;
  text-align: center;
  padding: 60px 0 80px 0;
  width: 100%; }
  .newsletter .news-caption {
    color: #999;
    font-size: 16px;
    margin: 20px; }
  .newsletter .field-container {
    display: flex;
    max-width: 60%;
    margin: auto; }

.post {
  margin: 60px 0;
  padding: 20px;
  transition: .3s all;
  border-radius: 5px;
  cursor: pointer; }
  .post:hover {
    box-shadow: 0px 1px 40px #e8e8e8;
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px); }
  .post:hover .learn {
    color: #0E00FF; }
  .post .date {
    color: #999;
    margin: 10px 0; }
  .post .post-title {
    font-size: 24px;
    margin: 10px 0; }
  .post .teaser {
    margin: 10px 0;
    color: #999; }

.projects-container {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 40px;
  padding: 40px;
  transition: 1s all; }
  .projects-container .sidebar .title {
    color: #000;
    font-weight: 600;
    margin-bottom: 20px;
    padding-top: 20px; }
  .projects-container .sidebar .item {
    margin: 20px 0;
    cursor: pointer;
    transition: .2s all;
    color: #999; }
    .projects-container .sidebar .item:hover {
      color: black; }
  .projects-container .sidebar .selected {
    color: #000; }
  .projects-container .projects-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    transition: 1s all; }
    @media only screen and (max-width: 750px) {
      .projects-container .projects-grid {
        grid-template-columns: 1fr; } }

