@import "../../master.scss";


.socials {
   margin: 20px;
   animation-name: socials;
   animation-duration: 1s;
   animation-delay: .3s;
   animation-fill-mode: forwards;
   opacity: 0;

   font-size: 22px;

   a {
      margin: 10px;
      transition: .2s all;
   }

   a:hover {
      color: $text_grey;
   }

   @keyframes socials {
      0% {
         opacity: 0;
         transform: translateY(20px);
      }

      100% {
         opacity: 1;
         transform: translateY(0px);
      }
   }



}
